.collectionitem {
  min-width: 23%;
  display: flex;
  flex-direction: column;
  height: 400px;
  align-items: center;
  position: relative;
}

.collectionitem .image {
  width: 100%;
  height: 95%;
  background-size: cover;
  background-position: center;
  margin-bottom: 5px;
  transition: 0.5s;
}

.collectionitem .collectionfooter {
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}

.collectionitem .collectionfooter .name {
  width: 90%;
  margin-bottom: 15px;
}

.collectionitem .collectionfooter .price {
  width: 10%;
}

.collectionitem .custom-button {
  position: absolute;
  height: 50px;
  top: 0px;
  display: none;
  transition: 0.5s;
  width: 80%;
}

.collectionitem .custom-button:hover {
  background-color: white;
  z-index: 3;
}

.collectionitem:hover .custom-button {
  position: absolute;
  display: block;
  top: 300px;
  opacity: 0.7;
  color: white;
  background-color: black;
}

.collectionitem:hover .image {
  background-color: black;
  opacity: 0.8;
}

@media screen and (max-width: 800px) {
  .collectionitem {
    min-width: 48%;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
  }

  .collectionitem .image {
    width: 100%;
    height: 90%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
    transition: 0.5s;
  }

  .collectionitem .collectionfooter {
    width: 90%;
    height: 5%;
    display: flex;
    justify-content: space-evenly;
    font-size: 15px;
  }

  .collectionitem .collectionfooter .name {
    width: 80%;
    margin-bottom: 15px;
  }

  .collectionitem .collectionfooter .price {
    width: 20%;
  }
  .collectionitem:hover .custom-button {
    position: absolute;
    display: block;
    top: 250px;
    opacity: 0.7;
    height: 35px;
    font-size: 10px;
    color: white;
    background-color: black;
  }
  .custom-button {
    position: absolute;
    display: block;
    top: 250px;
    opacity: 1;
    height: 35px;
    font-size: 10px;
    color: white;
    background-color: black;
  }

  .collectionitem:hover .image {
    background-color: black;
    opacity: 0.8;
  }
}
