.custom-button {
  min-width: 165px;
  height: 50px;
  line-height: 50px;
  letter-spacing: 0.5px;
  padding: 0 35px 0 35px;
  font-size: 15px;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  border: none;
  font-weight: bolder;
  cursor: pointer;
  transition: 0.5s ease-in;
  margin-bottom: auto;
}

.custom-button:hover {
  color: black;
  background-color: white;
  border: 1px solid black;
}

.google-sign-in {
  min-width: 165px;
  height: 50px;
  line-height: 50px;
  letter-spacing: 0.5px;
  padding: 0 35px 0 35px;
  font-size: 15px;
  text-transform: uppercase;
  font-family: "Open Sans Condensed", sans-serif;
  border: none;
  font-weight: bolder;
  cursor: pointer;
  transition: 0.5s ease-in;
  background-color: #4285f4;
  color: blanchedalmond;
}

.google-sign-in:hover {
  background-color: white;
  border: none;
  color: black;
}

@media screen and (max-width: 800px) {
  .custom-button {
    min-width: 120px;
    height: 28px;
    line-height: 28px;
    letter-spacing: 0.5px;
    padding: 0 5px 0 5px;
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    border: none;
    font-weight: bolder;
    cursor: pointer;
    transition: 0.5s ease-in;
    margin-bottom: auto;
  }

  .custom-button:hover {
    color: black;
    background-color: white;
    border: 1px solid black;
  }
  .google-sign-in {
    min-width: 120px;
    height: 28px;
    line-height: 28px;
    padding: 0 5px 0 5px;
    font-size: 15px;
    text-transform: uppercase;
    font-family: "Open Sans Condensed", sans-serif;
    border: none;
    font-weight: bolder;
    cursor: pointer;
    transition: 0.5s ease-in;
    background-color: #4285f4;
    color: blanchedalmond;
  }

  .google-sign-in:hover {
    background-color: white;
    border: none;
    color: black;
  }
}
