.cart-dropdown {
  position: absolute;
  width: 300px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;
}

.cart-dropdown .empty-message {
  font-size: 18px;
  margin: 50px auto;
}

.cart-dropdown .cart-items {
  height: 240px;
  display: flex;
  flex-direction: column;
  overflow: auto;
  overflow-x: hidden;
}

.cart-dropdown .button {
  width: 100%;
  height: 50px;
  letter-spacing: 0.5px;
  font-size: 15px;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  border: none;
  font-weight: bolder;
  cursor: pointer;
  transition: 0.5s ease-in;
  margin-bottom: 0;
}

.cart-dropdown .button:hover {
  color: black;
  background-color: white;
  border: 1px solid black;
}

@media screen and (max-width: 800px) {
  .cart-dropdown {
    position: absolute;
    width: 200px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 8px;
    border: 1px solid black;
    background-color: white;
    top: 90px;
    right: 40px;
    z-index: 5;
  }

  .cart-dropdown .empty-message {
    font-size: 18px;
    margin: 50px auto;
  }

  .cart-dropdown .cart-items {
    height: 280px;
    display: flex;
    flex-direction: column;
    overflow: auto;
  }
  .cart-dropdown .button {
    width: 100%;
    height: 40px;
    letter-spacing: 0.5px;
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    border: none;
    font-weight: bolder;
    cursor: pointer;
    transition: 0.5s ease-in;
    bottom: 0px;
  }

  .cart-dropdown .button:hover {
    color: black;
    background-color: white;
    border: 1px solid black;
  }
}
