.menu-list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 240px;
  flex: 1 1 auto;
  min-width: 30%;
  margin: 10px 7.5px 15px;
  border: 2px solid black;
  overflow: hidden;
}
.menu-list:hover {
  cursor: pointer;
}
.menu-list:hover .background-image {
  transform: scale(1.4);
  transition: transform 6s ease-out;
}
.menu-list:hover .content {
  opacity: 0.9;
}
.menu-list .background-image {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.menu-list.large {
  height: 380px;
}
.menu-list:first-child {
  margin-right: 7.5px;
}
.menu-list:last-child {
  margin-left: 7.5px;
}
.menu-list .content {
  height: 90px;
  padding: 0 25px;
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  background-color: white;
  opacity: 0.7;
  position: absolute;
}
.menu-list .title {
  font-weight: bold;
  margin-bottom: 6px;
  font-size: 22px;
  color: lightslategrey;
  text-transform: uppercase;
  text-align: center;
}
.menu-list .sibtitle {
  font-weight: lighter;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
}

@media screen and (max-width: 800px) {
  .menu-list {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 240px;
    flex: 1 1 auto;
    min-width: 50%;
    margin: 10px 7.5px 15px;
    border: 2px solid black;
    overflow: hidden;
  }
  .menu-list:hover {
    cursor: pointer;
  }
  .menu-list:hover .background-image {
    transform: scale(1.4);
    transition: transform 6s ease-out;
  }
  .menu-list:hover .content {
    opacity: 0.9;
  }
  .menu-list .background-image {
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
  }

  .menu-list .content {
    height: 80px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    border: 2px solid black;
    background-color: white;
    opacity: 0.7;
    position: absolute;
  }
  .menu-list .title {
    font-weight: bold;
    margin-bottom: 6px;
    font-size: 20px;
    align-items: center;
    text-transform: uppercase;
    text-align: center;
  }
  .menu-list .sibtitle {
    font-weight: lighter;
    font-size: 14px;
    text-transform: uppercase;
    text-align: center;
  }
}
