.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;
}

.checkout-item .image-container {
  width: 23%;
  padding-right: 15px;
}

.checkout-item .image-container img {
  width: 100%;
  height: 100%;
}

.checkout-item .name,
.checkout-item .quantity,
.checkout-item .price {
  width: 23%;
}

.checkout-item .quantity {
  padding-left: 20px;
  display: flex;
}

.checkout-item .quantity .arrow {
  cursor: pointer;
}

.checkout-item .remove {
  padding-left: 12px;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgrey;
    padding: 5px 0;
    font-size: 20px;
    align-items: center;
  }

  .checkout-item .image-container {
    width: 25%;
    padding-right: 15px;
  }

  .checkout-item .image-container img {
    width: 100%;
  }

  .checkout-item .name,
  .checkout-item .quantity,
  .checkout-item .price {
    width: 23%;
    align-items: center;
    justify-content: center;
    font-size: 15px;
  }

  .checkout-item .quantity {
    padding-left: 2px;
    text-align: center;
    display: inline;
    padding-right: 20px;
    font-size: 13px;
  }

  .checkout-item .quantity .arrow {
    cursor: pointer;
  }

  .checkout-item .remove {
    padding-left: 12px;
    cursor: pointer;
    font-size: 15px;
  }
}
