.signup {
  width: 380px;
  display: flex;
  flex-direction: column;
}

.signup .title {
  margin: 10px 0;
}

.signup .buttons {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 800px) {
  .signup {
    width: 280px;
    display: flex;
    flex-direction: column;
  }

  .signup .title {
    margin: 10px 0;
  }

  .signup .buttons {
    display: flex;
    justify-content: space-between;
    font-size: 10px;
  }
}
